import { createStore } from 'vuex'

export default createStore({
  state: {
		cartLists:[
			{id:'101',name:'苹果',desc:'陕西红富士',price:10,nums:1},
			{id:'101',name:'苹果',desc:'陕西红富士',price:10,nums:1},
			{id:'101',name:'苹果',desc:'陕西红富士',price:10,nums:1},
			{id:'101',name:'苹果',desc:'陕西红富士',price:10,nums:1},
		]
  },
	getters: { /* 通过this.$store.getters.getlist 调用 */
		getCartLists: function(state) {
			return state.cartLists;
		},
	},
  mutations: {
		addNum: function(state, data) {
			state.cartLists[data.index].nums++;
		},
		reduceNum: function(state, data) {
			if(state.cartLists[data.index].nums>1){
				state.cartLists[data.index].nums--;
			}
		},
		delGood:function(state,data){
			state.cartLists.splice(data.index,1);
		},
		addCart:function(state,data){
			let res = state.cartLists.findIndex((ele,index)=>{
				return ele.id==data.id;
			});
			if(res==-1){
				state.cartLists.push(data);
			}else{
				state.cartLists[res].nums++;
			}
		}
  },
  actions: {
		cmAddNum: function({commit}, data) {
			commit('addNum', data);
		},
		cmReduceNum: function({commit}, data) {
			commit('reduceNum', data);
		},
		cmDelGood:function({commit},data) {
			commit('delGood',data);
		},
		cmAddCart:function({commit},data) {
			commit('addCart',data);
		}
  },
  modules: {
  }
})
