
import {defineComponent,getCurrentInstance,ref,toRefs,reactive,onMounted,watch,computed} from 'vue';
import {useStore} from 'vuex'
import {useRoute,useRouter} from 'vue-router'
export default defineComponent({
	name: 'Cart',
	components: {},
	setup(props, context) {
		const route:any = useRoute();
		const router:any = useRouter();
		const that:any = getCurrentInstance();
		onMounted(()=>{
			navvisited[route.name as keyof typeof navvisited]=true;
		});
		const searchkey = ref(null);
		const navvisited = reactive({
			Homec:false,
			Sort:false,
			Hot:false,
			My:false
		})
		const isclick = (val:string)=>{
			router.push({name:val,params:{}});
			Object.keys(navvisited).forEach((key)=>{
				navvisited[key as keyof typeof navvisited]=false; 
			});
			navvisited[val as keyof typeof navvisited]=true;
		}
		const swipeImgs = ref([
			require("@/assets/ad1.jpg"), 
			require("@/assets/ad2.jpg"),
			require("@/assets/ad3.jpg"),
			require("@/assets/ad4.jpg"),
			]);
		return{navvisited,isclick,searchkey,swipeImgs}
	}
});
